<template>
  <div>
    <Table
      border
      stripe
      no-data-text="并没有关注任何人"
      :loading="loading"
      :columns="columns1"
      :data="focusList"
    ></Table>
    <Row>
      <Col span="4" offset="4">
        总共{{total}}条数据
      </Col>
      <Col>
        <Page :total="total" :current="pageNum" show-elevator @on-change="pageChange"/>
      </Col>
    </Row>
  </div>
</template>

<script>
import bnfocus from '../page/BtFocus'
  export default {
    name: "FocusList",
    inject: ['reload'],
    data() {
      return {
        pages: 0,
        pageSize: 10,
        pageNum: 1,
        focusList: [],
        loading: true,
        total: 0,
        columns1: [
          {
            title: '用户名',
            render: (h, params) => {
              return h('Button', {
                on:{
                  click(){
                    /*this.$router.push('/personHome/')*/
                    self.location.href="#/personHome/"+params.row.user.userId
                  }
                },props:{
                  type:'text'
                }
              },params.row.user.userName)
            }
          },
          {
            title: '个性签名',
            render: (h, params) => {
              if (params.row.user.userWords == '' || params.row.user.userWords == null) {
                return h('div', {style: 'color:lightseagreen'}, "这个人很懒什么都没写~~~")
              } else {
                return h('div', {style: ''}, params.row.user.userWords)
              }
            }
          }, {
            title: "性别",
            render: (h, params) => {
              if (params.row.user.userGender === 'M') {
                return h('div', {style: 'color:deepskyblue;font-weight:bold'}, '男')
              } else {
                return h('div', {style: 'color:deeppink;font-weight:bold'}, '女')
              }
            },
            filters: [          //筛选
              {
                label: '男',
                value: 'M'
              }, {
                label: '女',
                value: 'F'
              }
            ], filterMultiple: false,//使用单选，默认为多选
            filterMethod(value, row) {
              if (value === 'F') {
                return row.user.userGender === 'F';
              } else {
                return row.user.userGender === 'M';
              }
            }
          }, {
            title: "邮箱",
            render: (h, params) => {
              return h('div', params.row.user.userEmail)
            },
            filters: [
              {
                label: 'qq邮箱',
                value: '@qq.com'
              },
              {
                label: '163邮箱',
                value: '@163.com'
              }
            ],
            filterMethod(value, row) {
              return row.user.userEmail.indexOf(value) > -1;
            }
          }, {
            title: "TAの粉丝数",
            render: (h, params) => {
              return h('div', params.row.user.userFansnum)
            },
            sortable: true,
          }, {
            title: "TAの关注数",
            sortable: true,
            render: (h, params) => {
              return h('div', params.row.user.userFocusnum)
            }
          }, {
            title: "操作",
            render: (h, params) => {
              if (this.userId == this.$store.state.userInfo.userId) {
                return h(bnfocus, {
                  props: {
                    row: params.row
                  }
                });
              }
            }
          }
        ],userId:0
      }
    }, methods: {
      getOnesFocus() {
        var params = new URLSearchParams();
        params.append('pageNum', this.pageNum);
        this.axios.get('focus/' + this.userId, {params: {pageNum: this.pageNum}}).then(response => {
          if (response.data.code === 100) {
            this.focusList = response.data.map.getUserFocus.list
            this.pages = response.data.map.getUserFocus.pages
            this.pageNum = response.data.map.getUserFocus.pageNum
            this.pageSize = response.data.map.getUserFocus.pageSize
            this.total = response.data.map.getUserFocus.total
          }
          this.loading = false
        }).catch(error => {
          this.$Message.warning("获取关注列表失败")
        })
      } , pageChange(page) {
        this.pageNum = page
      },
  }
  ,
  components: {
      bnfocus
  }
  ,
  mounted()
  { this.userId=this.$route.params.userId
    this.getOnesFocus()
  }
  ,
  watch:{
    pageNum:function () {
      this.getOnesFocus()
    }
  }
  }
</script>

<style scoped>

</style>



