<template>
  <div :row="row">
    <Button type="success" v-if="flag" @click="handleCancleFocus">互相互关</Button>
    <Button type="error"  @click="handleCancleFocus" v-else>取消关注</Button>
  </div>
</template>

<script>
    export default {
        name: "bn",
      inject: ['reload'],
      data() {
            return {
              flag:false,
            }
        }, methods: {
        handleCancleFocus() {
          this.axios.delete('focus', {
            params: {
              focusUserid: this.row.focusUserid,
              focusBeuserid: this.row.focusBeuserid
            }
          }).then(response => {
            if (response.data.code === 100) {
              this.$Message.success("取消关注")
              this.$store.commit('setFocusNumSub')
              this.reload()
            } else {
              this.$Message.error("取关失败")
            }
          }).catch(error => {
            this.$Message.warning("取消关注失败")
          })
        }, checkFocusOr() {
          this.flag=false
          this.axios.get('fansCheck', {params: {focusUserid: this.row.focusBeuserid, focusBeuserid: this.row.focusUserid}})
            .then(res => {
              if (res.data.code === 100) {
                //console.log("他也是你的粉丝")
                this.flag=true
              }
            }).catch(error => {
            this.$Message.warning("检查是否关注出错")
          })
        }
      }, components: {}, mounted() {
        this.checkFocusOr(this.row)
      }, props: ['row']
    }
</script>

<style scoped>

</style>



